
import Msg from 'vux/src/components/msg/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
Msg,
Divider,
XButton
},
mounted () {
this.ret = this.$route.params.ret
if(this.ret.isSucc === 0) {
this.icon = 'success'
}else{
this.icon = 'warn'
}
},
methods: {
changeIcon () {
this.$vux.alert.show({
content: ``
})
}
},
data () {
return {
description: 'msg description',
icon: '',
ret:null,
buttons: [{
type: 'primary',
text: '',
onClick: this.changeIcon.bind(this)
}, {
type: 'default',
text: '',
link: '/home/yixue'
}]
}
}
}



