
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState, mapActions } from 'vuex'
export default {
components : { FormPreview, Cell },
data () {
return {
applyList : [],
headerlabel:""
}
},
mounted () {
this.getAppliCash()
},
computed: {
...mapState({
sellerInfo : state => state.user.sellerInfo
})
},
methods: {
...mapActions([
'applyCash_history'
]),
async getAppliCash () {
let res = await this.applyCash_history()
let self = this
res.data.map(applyItem => {
let listItem = {
cash : "" + applyItem.cash,
Items:[]
}
listItem.Items.push({
label: '',
value: transForShow(applyItem.status)
})
listItem.Items.push({
label: '',
value: dateFormat(new Date(applyItem.createTime),'yyyy-MM-dd hh:mm:ss')
})
if (applyItem.endTime !== 0) {
listItem.Items.push({
label: '',
value: dateFormat(new Date(applyItem.endTime),'yyyy-MM-dd hh:mm:ss')
})
}
if (applyItem.detail) {
listItem.Items.push({
label: '',
value: applyItem.detail
})
}
self.applyList.unshift(listItem)
})
}
}
}
function transForShow (num) {
/*
:
-3:
-2 
-1,
0;
2
1;
*/
switch (num) {
case -3 :
return ""
case -2 :
return ""
case -1 :
return ""
case 0 :
return ""
case 1 :
return ""
case 2 :
return ""
default :
return ':' + num

}
}
const dateFormat = function (date,fmt) {
let o = {
"M+": date.getMonth() + 1,
"d+": date.getDate(),
"h+": date.getHours(),
"m+": date.getMinutes(),
"s+": date.getSeconds(),
"q+": Math.floor((date.getMonth() + 3) / 3),
"S": date.getMilliseconds()
}
if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length))
for (var k in o) {
if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)))
}
return fmt
}



