    */
    // this.$router.push(url)
    // url = 'http://www.baidu.com'
    // alert(url)
    this.$router.push(url)
  },
  methods: {
    ...mapActions(["login"])
  },
  computed: {
    ...mapState({
      state: state => state,
      route: state => state.route,
      path: state => state.route.path
